import PlantItem from './PlantItem'
import '../styles/ShoppingList.css'
import Categories from './Categories'

function ShoppingList({cart, updateCart, displayedPlant, updateDisplayedPlant}) {
	//const [displayedPlant, updateDisplayedPlant] = useState([])
	//const active = displayedPlant

	function addToCart(name, price){
		// Parcours le tableau cart pour trouver si un Ã©lÃ©ment respecte une condition
		// et le retourne
		// Ici on cherche si l'Ã©lÃ©ment qu'on essaye d'ajouter est dÃ©jÃ  dans le panier
		// si oui on va incrÃ©menter sa quantitÃ©
		
		const currentPlantAdded = cart.find((plant)=> plant.name===name)
		if (currentPlantAdded) {
			// filtre un tableau cart
			// cela permet de crÃ©er un nouveau tableau avec uniquement les Ã©lÃ©ment respectant un condition
			const cartFilteredCurrentPlant = cart.filter(
				(plant) => plant.name !== name
			)
			// Le spread opÃ©rator "..." permet d'incrÃ©menter un tableau <=> tab = tab + newElement
			updateCart([
				...cartFilteredCurrentPlant,
				{ name, price, amount: currentPlantAdded.amount + 1}
			])
		} else {
			updateCart([...cart, {name, price, amount:1}])
		}		
	}

	return (
		<div>
			<Categories displayedPlant={displayedPlant} updateDisplayedPlant={updateDisplayedPlant}/>
			<ul className='lmj-plant-list'>
				{displayedPlant.map(({name, id, cover, light, water, price}) => (
				<div key={id}>
					<PlantItem
						name={name}
						cover={cover}
						light={light}
						water={water}
					/>
					<button onClick={()=>addToCart(name, price)}>Ajouter</button>
				</div>		
				))}
			</ul>
		</div>
	)
}

export default ShoppingList
